<template>
  <div class="newBody">
    <div class="messages">
      <h1>中国农业方面讨论区</h1>
      <div class="form">
        <input
          type="text"
          id="username"
          v-model="username"
          placeholder="用户名"
        />
        <textarea
          id="message"
          v-model="message"
          placeholder="请输入留言内容..."
        ></textarea>
        <button id="submitBtn" @click="handleSubmit">提交</button>
      </div>
      <div id="messageBoard">
        <div
          v-for="(messageItem, index) in messageList"
          :key="index"
          class="message"
        >
          <div class="message-info">
            <div class="info">
              <img src="../assets/001.jpg" />
              <strong>{{ messageItem.username }}</strong>
            </div>
            <span>发布于, {{ messageItem.timestamp }}</span>
          </div>
          <div class="sontent">
            {{ messageItem.message }}
            <button id="dlet" @click="handleDelete(index)">删除</button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      username: "",
      message: "",
      messageList: [],
    };
  },
  methods: {
    handleSubmit() {
      if (this.message.trim() === "") {
        alert("内容为空，请输入内容！");
        return;
      }

      const newMessage = {
        username: this.username || "匿名",
        message: this.message,
        timestamp: this.getCurrentTime(),
      };

      this.messageList.unshift(newMessage);
      this.saveToLocalStorage();

      this.resetForm();
    },
    saveToLocalStorage() {
      localStorage.setItem("messageList", JSON.stringify(this.messageList));
    },
    loadFromLocalStorage() {
      const storedMessages = localStorage.getItem("messageList");
      if (storedMessages) {
        try {
          this.messageList = JSON.parse(storedMessages);
        } catch (error) {
          console.error("Error parsing messages from local storage:", error);
        }
      }
    },
    handleDelete(index) {
      this.messageList.splice(index, 1);
      this.saveToLocalStorage();
    },
    getCurrentTime() {
      const now = new Date();
      const year = now.getFullYear();
      const month = ("0" + (now.getMonth() + 1)).slice(-2);
      const day = ("0" + now.getDate()).slice(-2);
      const hours = ("0" + now.getHours()).slice(-2);
      const minutes = ("0" + now.getMinutes()).slice(-2);
      const seconds = ("0" + now.getSeconds()).slice(-2);
      return `${year}/${month}/${day} ${hours}:${minutes}:${seconds}`;
    },
    resetForm() {
      this.username = "";
      this.message = "";
    },
  },
  created() {
    this.loadFromLocalStorage();
  },
};
</script>

